@import url('https://font.googleapis.com/css2?family=Montserrat:wght@200;400;500;700;900$display=swap');

// All project colors
:root {
    --primaryColor: hsl(200, 80%, 50%);
    --primaryColorLight: hsl(42, 80%, 70%);
    --bgColor: hsl(0, 0%, 100%);
    --formColor: hsl(20, 42%, 87%);
    --whiteColor: hsl(0, 0%, 100%);
    --blackColor: hsl(0, 0%, 18%);
    --greyColor: hsl(0, 0%, 51%);
    --inputColor: hsl(330, 12%, 97%);
    --cardColor: hsl(300, 3%, 94%);

    // Font and Typography
    --biggestFontSize: 2.5rem;
    --h1FontSize: 1.6rem;
    --h2FontSize: 1.25rem;
    --h3FontSize: 1rem;
    --normalFontSize: 0.938rem;
    --smallFontSize: 0.813rem;
    --smallestFontSize: 0.75rem;
    --borderRadius: 3rem;
}

.btn11 {
    color: var(--whiteColor);
    background: var(--primaryColor);

    &:hover {
        transition: .3s ease;
    }
}

.navList a {
    color: var(--blackColor);
    text-decoration: none;
    transition: color 0.3s ease-in-out;

    &.active {
        color: var(--primaryColor) !important;
        // border-bottom: 1.5px solid var(--primaryColor);
    }
  
    &:hover {
        color: var(--primaryColor);
    }
}

.navBar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 1.5rem 0.7rem;
    position: fixed;
    background: rgba(255, 255, 255, 0.737);
    border-bottom: 1.5px solid rgb(211, 211, 211);
    backdrop-filter: blur(5px);
    z-index: 1000;

    .logoDiv {
        display: flex;
        align-items: center;
        justify-content: center;

        .logo1 {
            height: 40px;
            width: 100px;
            padding: 0 10px;
        }

        span {
            color: var(--blackColor);
            font-size: var(--h3FontSize);
            font-weight: bold;
            justify-content: center;
        }
    }

    @media (max-width: 767px) {
        .menu {
            position: absolute;
            width: 70%;
            top: -100rem;
            right: 50%;
            left: 50%;
            transform: translate(-50%);
            background: rgba(255, 255, 255, 0.94);
            padding: 2rem;
            border-radius: 10px;
            transition: .8s ease-in-out;
            backdrop-filter: blur(5px);
            box-shadow: 0 2px 3px 4px rgba(236, 213, 213, 0.516);

            li {
                font-size: var(--h3FontSize);
                font-weight: 500;
                padding: 1rem 0;
                text-align: center;
                cursor: pointer;

                &:hover {
                    color: var(--primaryColor);
                    border-bottom: 1.5px solid var(--primaryColor);
                }
            }
        }
    }

    .menuIcon, .closeIcon {
        font-size: var(--h1FontSize);

        &:hover {
            color: var(--primaryColor);
        }
    }

    .closeIcon {
        position: absolute;
        bottom: -15px;
        right: 50%;
        left: 50%;
        color: var(--blackColor);
        transform: translate(-50%);
    }

    .showNavbar {
        top: 8rem;
    }
}

// Media queries
@media (min-width: 768px) {
    .navBar {
        padding: 1.5rem 2rem;

        .logoDiv {
            span {
                font-size: var(--h2FontSize);
            }
        }

        .closeIcon, .menuIcon {
            display: none;
        }

        .menu {
            ul {
                display: flex;
                gap: 1rem;

                li {
                    font-size: var(--h3FontSize);
                    font-weight: 500;
                    padding: 1rem 0;
                    text-align: center;
                    cursor: pointer;

                    &:hover {
                        color: var(--primaryColor);
                        border-bottom: 1.5px solid var(--primaryColor);
                    }
                }
            }
        }
    }
}

@media (min-width: 1240px) {
    .navBar {
        padding: 2rem 6rem;

        .menu {
            ul {
                gap: 4rem;
            }
        }
    }
}

@media (min-width: 1440px) {
    .navBar {
        padding: 2rem 14rem;
    }
}
