@import url("https://font.googleapis.com/css2?family=Montserrat:wght@200;400;500;700;900$display=swap");
:root {
  --primaryColor: hsl(200, 80%, 50%);
  --primaryColorLight: hsl(42, 80%, 70%);
  --bgColor: hsl(0, 0%, 100%);
  --formColor: hsl(20, 42%, 87%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 18%);
  --greyColor: hsl(0, 0%, 51%);
  --inputColor: hsl(330, 12%, 97%);
  --cardColor: hsl(300, 3%, 94%);
  --biggestFontSize: 2.5rem;
  --h1FontSize: 1.6rem;
  --h2FontSize: 1.25rem;
  --h3FontSize: 1rem;
  --normalFontSize: 0.938rem;
  --smallFontSize: 0.813rem;
  --smallestFontSize: 0.75rem;
  --borderRadius: 3rem;
}

.subscribe .secContainer img {
  height: 300px;
}
.subscribe .secContainer .textDiv h4 {
  font-size: var(--h2FontSize);
  color: var(--blackColor);
}
.subscribe .secContainer .textDiv p {
  padding: 1rem 0;
}
.subscribe .secContainer .textDiv .btn {
  padding: 0.5rem 2rem;
}

@media (min-width: 480px) {
  .subscribe .secContainer img {
    max-width: 75%;
    max-width: auto;
  }
  .subscribe .secContainer .textDiv h4 {
    font-size: 1.5rem;
  }
  .subscribe .secContainer .textDiv p {
    padding: 1rem 0;
    font-size: var(--h3FontSize);
  }
  .subscribe .secContainer .textDiv .btn {
    padding: 0.5rem;
    font-size: var(--h2FontSize);
  }
}
@media (min-width: 640px) {
  .subscribe .secContainer {
    grid-template-columns: repeat(2, 1fr);
  }
  .subscribe .secContainer img {
    width: 100%;
    margin: auto;
  }
  .subscribe .secContainer .textDiv h4 {
    font-size: 2rem;
  }
  .subscribe .secContainer .textDiv p {
    font-size: var(--h3FontSize);
    padding: 1rem 0;
  }
}
@media (min-width: 1024px) {
  .subscribe .secContainer .textDiv h4 {
    font-size: var(--h1FontSize);
  }
  .subscribe .secContainer .textDiv p {
    padding: 2rem 0;
    font-size: var(--h2FontSize);
  }
}/*# sourceMappingURL=Subscribe.css.map */