@import url('https://font.googleapis.com/css2?family=Montserrat:wght@200;400;500;700;900$display=swap');

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
}

html {
    scroll-behavior: smooth;
    scroll-padding-top: 70px; 
}
  

// All project colors

// --primaryColor: hsl(14, 90%, 53%);
// --primaryColorLight: hsl(14, 96%, 91%);

:root {
    --primaryColor: hsl(200, 80%, 50%);
    --primaryColorLight: hsl(42, 80%, 70%);
    --bgColor: hsl(0, 0%, 100%);
    --formColor: hsl(20, 42%, 87%);
    --whiteColor: hsl(0, 0%, 100%);
    --blackColor: hsl(0, 0%, 18%);
    --greyColor: hsl(0, 0%, 51%);
    --inputColor: hsl(330, 12%, 97%);
    --cardColor: hsl(300, 3%, 94%);

    // Font and Topography
    --biggestFontSize: 2.5rem;
    --h1FontSize: 1.6rem;
    --h2FontSize: 1.25rem;
    --h3FontSize: 1rem;
    --normalFontSize: 0.938rem;
    --smallFontSize: 0.813rem;
    --smallestFontSize: 0.75rem;
    --borderRadius: 3rem;
}

body {
    background: var(--bgColor);
}


//default styling
.flex {
    display: flex;
    align-items: center;
}

.grid {
    display: grid; //changed from flex to grid
    align-items: center;
    gap: 1.5rem;
}

p {
    font-size: var(--smallFontSize);
}

html {
    font-size: 90%;
}

a {
    text-decoration: none;
}

li {
    list-style: none;
}

.icon {
    font-size: var(--h2FontSize);
    cursor: pointer;
}

img, video {
    height: auto;
    width: 100%;
}

.btn {
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: var(--borderRadius);
    padding: 0.8rem 1rem;
    color: var(--whiteColor);
    background: var(--primaryColor);

    &:hover {
        transition: .3s ease;
    }
}

.secTitle {
    display: grid; //changed from none to grid
    text-align: center;
    padding: 1rem 0;
    font-size: 25px;
    font-weight: 700;
    color: var(--blackColor);
    flex-direction: column;
    gap: 1rem;

    p {
        color: var(--blackColor);
        font-size: var(--normalFontSize);
        font-weight: 400;
    }

    h3 {
        font-size: var(--h1FontSize);
    }
}

.secHeading {
    padding-bottom: 1.5rem;
}

.btn .icon {
    margin-left: 0.5em;
}

.btn:hover {
    color: var(--primaryColor);
    background: var(--primaryColorLight);
}

.section {
    // padding: 4rem 0.2rem;
    padding: 3rem 0.2rem;
}

.container {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
}

::selection {
    background: var(--primaryColorLight);
    color: var(--primaryColor);
}

::-webkit-scrollbar {
    width: 10px;
    background: var(--primaryColorLight);
}

::-webkit-scrollbar-thumb {
    width: 10px;
    background: var(--primaryColor);
    border-radius: var(--borderRadius);
}

//All this styling will take effect / apply in later stage

//media queries 
@media (min-width: 840px) {
    .section {
        // padding: 4rem 0 3rem;
        padding: 3rem 0 2rem;
    }

    .container {
        width: 75%;
        margin: auto;
    }
}
