// All project colors
:root {
    --primaryColor: hsl(200, 80%, 50%);
    --primaryColorLight: hsl(42, 80%, 70%);
    --bgColor: hsl(0, 0%, 100%);
    --formColor: hsl(20, 42%, 87%);
    --whiteColor: hsl(0, 0%, 100%);
    --blackColor: hsl(0, 0%, 18%);
    --greyColor: hsl(0, 0%, 51%);
    --inputColor: hsl(330, 12%, 97%);
    --cardColor: hsl(300, 3%, 94%);

    // Font and Topography
    --biggestFontSize: 2.5rem;
    --h1FontSize: 1.6rem;
    --h2FontSize: 1.25rem;
    --h3FontSize: 1rem;
    --normalFontSize: 0.938rem;
    --smallFontSize: 0.813rem;
    --smallestFontSize: 0.75rem;
    --borderRadius: 3rem;
}


.footer {
    background: var(--primaryColorLight);
    padding: 2rem 0;

    .secContainer {
        gap: 1.5rem;
        text-align: center;
        align-items: flex-start;

        .logoDiv {
            .footerLogo {
                display: flex;
                color: var(--blackColor);
                font-weight: 500;
                cursor: pointer;
                align-items: center;
                justify-content: center;
                padding-bottom: 1rem;

                .logo1 {
                    // font-size: 30px;
                    // color: var(--blackColor);
                    height: 50px;
                    width: 100px;
                }
            }

            .socials {
                gap: .5rem;
                margin: auto;
                align-items: center;
                justify-content: center;

                .icon {
                    // font-size: 25px;
                    font-size: 30px;
                    background: var(--primaryColor);
                    border-radius: 50%;
                    padding: 8px;
                    color: var(--whiteColor);
                    &:hover {
                        background: var(--primaryColorLight);
                        color: var(--primaryColor);
                    }
                }
            }
        }

        .footerLinks {
            display: grid;
            row-gap: .2rem;

            .linkTitle {
                padding-bottom: .5rem;
                color: var(--blackColor);
                font-weight: 700;
            }

            li {
                &:hover {
                    transform: translate(10px);
                    a {
                        color: var(--primaryColor);
                    }
                }
            }

            a, .phone, .email {
                color: var(--blackColor);
                font-size: 13px;
                font-weight: 500;
            }
        }
    }
}


//media queries

@media (min-width: 448px) {
    .secContainer {
        grid-template-columns: repeat(2, 1fr);
        text-align: start !important;
        align-items: center;

        .footerLinks {
            justify-content: center;
        }
    }
}


@media (min-width: 640px) {
    .secContainer {
        grid-template-columns: repeat(4, 1fr);
    }
}


@media (min-width: 640px) {
    .secContainer {
        padding: 2rem 0;
        grid-template-columns: repeat(4, 1fr);
    }
}


