@import url('https://font.googleapis.com/css2?family=Montserrat:wght@200;400;500;700;900$display=swap');

// All project colors
:root {
    --primaryColor: hsl(200, 80%, 50%);
    --primaryColorLight: hsl(42, 80%, 70%);
    --bgColor: hsl(0, 0%, 100%);
    --formColor: hsl(20, 42%, 87%);
    --whiteColor: hsl(0, 0%, 100%);
    --blackColor: hsl(0, 0%, 18%);
    --greyColor: hsl(0, 0%, 51%);
    --inputColor: hsl(330, 12%, 97%);
    --cardColor: hsl(300, 3%, 94%);

    // Font and Topography
    --biggestFontSize: 2.5rem;
    --h1FontSize: 1.6rem;
    --h2FontSize: 1.25rem;
    --h3FontSize: 1rem;
    --normalFontSize: 0.938rem;
    --smallFontSize: 0.813rem;
    --smallestFontSize: 0.75rem;
    --borderRadius: 3rem;
}

.review {
    .secContainer {
        .textDiv {
            .redText {
                font-size: var(--smallFontSize);
                display: block;
                color: var(--primaryColor);
                font-weight: 700;
            }

            h3 {
                font-size: var(--h2FontSize);
                color: var(--blackColor);
                padding: 1rem 0;
            }

            p {
                color: var(--blackColor);
            }

            .stars {
                gap: .4rem;
                padding: 1rem 0;
                .icon {
                    color: var(--primaryColor);
                    font-size: var(--h3FontSize);
                }
            }

            .clientsImages {
                gap: 1rem;
                img {
                    height: 40px;
                    width: 40px;
                    border-radius: 50%;
                    object-fit: cover;
                    border: 2px solid rgb(255, 255, 255);
                    box-shadow: 0 0 2px 2px rgb(251, 212, 194);
                }
            }
        }

        .imgDiv {
            height: 180px;
            width: 100%;
            border-radius: 10px;
            overflow: hidden;
            img {
                object-fit: cover;
            }
        }

    }
}


//media queries

@media (min-width: 480px) {
    .review {
        .secContainer {
            .textDiv {
                .redText {
                    font-size: var(--normalFontSize);
                }

                h3 {
                    font-size: 20px;
                    color: var(--blackColor);
                }
                p {
                    color: var(--blackColor);
                    font-size: var(--normalFontSize);
                }

                .stars {
                    .icon {
                        font-size: var(--h2FontSize);
                    }
                }

                .clientsImages {
                    img {
                        height: 50px;
                        width: 50px;
                    }
                }
            }

            .imgDiv {
                height: 250px;
                width: 100%;
                img {
                    height: 100%;
                    width: 100%;
                }
            }
        }
    }
}


@media (min-width: 740px) {
    .review {
        .secContainer {
            grid-template-columns: repeat(2,1fr);

            .textDiv {
                .redText {
                    font-size: var(--normalFontSize);
                }

                h3 {
                font-size: 20px;
                color: var(--blackColor);
                }
                p {
                font-size: var(--normalFontSize);
                }

                .stars {
                    .icon {
                        font-size: var(--h2FontSize);
                    }
                }

                .clientsImages {
                    img {
                     height: 50px;
                     width: 50px;
                    }
                }
            }
        }    
    }    
}


@media (min-width: 1240px) {
    .review {
        .secContainer {
            gap: 3rem;

            .textDiv {
                .redText {
                    font-size: var(--h3FontSize);
                }

                h3 {
                    font-size: var(--h1FontSize);
                    color: var(--blackColor);
                }
                p {
                    font-size: var(--h3FontSize);
                    color: var(--blackColor);
                    line-height: 2rem;
                }
            }

            .imgDiv {
                height: 300px;
            }
        }
    }
}