@import url('https://font.googleapis.com/css2?family=Montserrat:wght@200;400;500;700;900$display=swap');

// All project colors
:root {
    --primaryColor: hsl(200, 80%, 50%);
    --primaryColorLight: hsl(42, 80%, 70%);
    --bgColor: hsl(0, 0%, 100%);
    --formColor: hsl(20, 42%, 87%);
    --whiteColor: hsl(0, 0%, 100%);
    --blackColor: hsl(0, 0%, 18%);
    --greyColor: hsl(0, 0%, 51%);
    --inputColor: hsl(330, 12%, 97%);
    --cardColor: hsl(300, 3%, 94%);

    // Font and Topography
    --biggestFontSize: 2.5rem;
    --h1FontSize: 1.6rem;
    --h2FontSize: 1.25rem;
    --h3FontSize: 1rem;
    --normalFontSize: 0.938rem;
    --smallFontSize: 0.813rem;
    --smallestFontSize: 0.75rem;
    --borderRadius: 3rem;
}

.Home {
    position: relative;
    height: 95vh;
    width: 100%;
    top: 0;

    .videoBg {
        position: absolute;
        height: 100%;
        width: 100%;

        video {
            height: 100%;
            width: 100%;
            object-fit: cover;
        }
    }

    .sectionText {
        position: absolute;
        height: 100%;
        top: 30%;
        bottom: 50%;
        color: var(--whiteColor);
        padding: 0 1.3rem;


        h1 {
            font-size: var(--biggestFontSize);
            font-weight: 900;
        }

        p {
            padding: 1rem 0;
            font-size: var(--normalFontSize);
            font-weight: 500;
        }

        .btn {
            gap: .5rem;
            padding-left: 2rem;
            padding-right: 2rem;
            font-size: var(--h3FontSize);
        }
    }

    .popularPlaces {
        display: none;
    }
}


//media queries 
@media (min-width: 480px) {
    .Home {
        .sectionText {
            padding: 0 2rem;
            justify-content: center;
            text-align: center;
            width: 100%;

            h1 {
                font-size: var(--biggestFontSize);
                font-weight: 900;
            }

            p {
                padding: 1rem 0;
                font-size: var(--h2FontSize);
                max-width: 70%;
                margin: auto;
            }

            .btn {
                font-size: var(--h3FontSize);
                margin: auto;
            }
        }
    }
}


@media (min-width: 769px) {
    .Home {
        .sectionText {
            width: 100%;

            h1 {
                font-size: 4rem;
                font-weight: 900;
                max-width: 70%;
                margin: auto;
            }

            p {
                padding: 1rem 0;
                font-size: var(--h2FontSize);
                max-width: 70%;
                margin: auto;
            }

            .btn {
                gap: .5rem;
                padding-right: 2rem;
                padding-left: 2rem;
                font-size: var(--h3FontSize);
                margin: auto;
            }
        }
    }
}


@mixin homeSection($bottom) {
    .Home {
      position: relative;
  
      .sectionText {
        h1 {
          font-size: 4.5rem;
          font-weight: 900;
          max-width: 70%;
          margin: auto;
        }
      }
  
      .popularPlaces {
        display: block;
        position: absolute;
        bottom: $bottom;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
  
        .content {
          h3 {
            color: var(--whiteColor);
            padding: 1rem 0;
            position: relative;
            &::after {
              content: '';
              width: 83%;
              height: 2px;
              background: rgb(202, 202, 202);
              right: 0;
              bottom: 50%;
              position: absolute;
            }
          }
  
          .images {
            align-items: center;
            justify-content: center;
            gap: 2rem;
  
            img {
              height: 100px;
              width: 210px;
              object-fit: cover;
              border-radius: 10px;
            }
          }
        }
      }
    }
  }

@media (min-width: 1040px) and (min-height: 520px) {
  @include homeSection(-12%);
}

@media (min-width: 1040px) and (min-height: 650px) {
  @include homeSection(-6%);
}

@media (min-width: 1040px) and (min-height: 1040px) {
  @include homeSection(-3%);
}


/* Preloader styles */
#preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--bg-color);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999; 
}

/* Spinner styles */
.spinner {
  width: 50px;
  height: 50px;
  border: 8px solid var(--box-color1);
  border-top: 8px solid var(--main-color);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

/* Animation */
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}



