@import url("https://font.googleapis.com/css2?family=Montserrat:wght@200;400;500;700;900$display=swap");
:root {
  --primaryColor: hsl(200, 80%, 50%);
  --primaryColorLight: hsl(42, 80%, 70%);
  --bgColor: hsl(0, 0%, 100%);
  --formColor: hsl(20, 42%, 87%);
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 18%);
  --greyColor: hsl(0, 0%, 51%);
  --inputColor: hsl(330, 12%, 97%);
  --cardColor: hsl(300, 3%, 94%);
  --biggestFontSize: 2.5rem;
  --h1FontSize: 1.6rem;
  --h2FontSize: 1.25rem;
  --h3FontSize: 1rem;
  --normalFontSize: 0.938rem;
  --smallFontSize: 0.813rem;
  --smallestFontSize: 0.75rem;
  --borderRadius: 3rem;
}

.btn11 {
  color: var(--whiteColor);
  background: var(--primaryColor);
}
.btn11:hover {
  transition: 0.3s ease;
}

.navList a {
  color: var(--blackColor);
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}
.navList a.active {
  color: var(--primaryColor) !important;
}
.navList a:hover {
  color: var(--primaryColor);
}

.navBar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 1.5rem 0.7rem;
  position: fixed;
  background: rgba(255, 255, 255, 0.737);
  border-bottom: 1.5px solid rgb(211, 211, 211);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  z-index: 1000;
}
.navBar .logoDiv {
  display: flex;
  align-items: center;
  justify-content: center;
}
.navBar .logoDiv .logo1 {
  height: 40px;
  width: 100px;
  padding: 0 10px;
}
.navBar .logoDiv span {
  color: var(--blackColor);
  font-size: var(--h3FontSize);
  font-weight: bold;
  justify-content: center;
}
@media (max-width: 767px) {
  .navBar .menu {
    position: absolute;
    width: 70%;
    top: -100rem;
    right: 50%;
    left: 50%;
    transform: translate(-50%);
    background: rgba(255, 255, 255, 0.94);
    padding: 2rem;
    border-radius: 10px;
    transition: 0.8s ease-in-out;
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    box-shadow: 0 2px 3px 4px rgba(236, 213, 213, 0.516);
  }
  .navBar .menu li {
    font-size: var(--h3FontSize);
    font-weight: 500;
    padding: 1rem 0;
    text-align: center;
    cursor: pointer;
  }
  .navBar .menu li:hover {
    color: var(--primaryColor);
    border-bottom: 1.5px solid var(--primaryColor);
  }
}
.navBar .menuIcon, .navBar .closeIcon {
  font-size: var(--h1FontSize);
}
.navBar .menuIcon:hover, .navBar .closeIcon:hover {
  color: var(--primaryColor);
}
.navBar .closeIcon {
  position: absolute;
  bottom: -15px;
  right: 50%;
  left: 50%;
  color: var(--blackColor);
  transform: translate(-50%);
}
.navBar .showNavbar {
  top: 8rem;
}

@media (min-width: 768px) {
  .navBar {
    padding: 1.5rem 2rem;
  }
  .navBar .logoDiv span {
    font-size: var(--h2FontSize);
  }
  .navBar .closeIcon, .navBar .menuIcon {
    display: none;
  }
  .navBar .menu ul {
    display: flex;
    gap: 1rem;
  }
  .navBar .menu ul li {
    font-size: var(--h3FontSize);
    font-weight: 500;
    padding: 1rem 0;
    text-align: center;
    cursor: pointer;
  }
  .navBar .menu ul li:hover {
    color: var(--primaryColor);
    border-bottom: 1.5px solid var(--primaryColor);
  }
}
@media (min-width: 1240px) {
  .navBar {
    padding: 2rem 6rem;
  }
  .navBar .menu ul {
    gap: 4rem;
  }
}
@media (min-width: 1440px) {
  .navBar {
    padding: 2rem 14rem;
  }
}/*# sourceMappingURL=Navbar.css.map */