// All project colors
:root {
    --primaryColor: hsl(200, 80%, 50%);
    --primaryColorLight: hsl(42, 80%, 70%);
    --bgColor: hsl(0, 0%, 100%);
    --formColor: hsl(20, 42%, 87%);
    --whiteColor: hsl(0, 0%, 100%);
    --blackColor: hsl(0, 0%, 18%);
    --greyColor: hsl(0, 0%, 51%);
    --inputColor: hsl(330, 12%, 97%);
    --cardColor: hsl(300, 3%, 94%);

    // Font and Topography
    --biggestFontSize: 2.5rem;
    --h1FontSize: 1.6rem;
    --h2FontSize: 1.25rem;
    --h3FontSize: 1rem;
    --normalFontSize: 0.938rem;
    --smallFontSize: 0.813rem;
    --smallestFontSize: 0.75rem;
    --borderRadius: 3rem;
}


.suggestions {
    list-style: none;
    margin-top: 5px;
    padding: 0;
    border: 1px solid #ddd;
    max-height: 150px;
    overflow-y: auto;
    background: white;

    .suggestion-item {
        padding: 10px;
        border-bottom: 1px solid #eee;
        &:hover {
        background-color: #f0f0f0;
        }
    }
}

  
.destination {
    .secContainer {
        .redText {
            color: var(--primaryColor);
            font-size: var(--smallFontSize);
            font-weight: 500;
        }

        h3 {
            font-size: var(--h1FontSize);
        }

        .searchField {
            margin-top: 2rem;
            gap: 1rem;

            .inputField {
                background: var(--primaryColorLight);
                padding: 1rem;
                border-radius: var(--borderRadius);
                gap: .5rem;

                input {
                    background: transparent;
                    border: none;
                    outline: none;
                    width: 100%;
                }

                .icon {
                    color: var(--blackColor);
                }
            }

            .btn {
                gap: .5rem;
                margin: auto;
                width: 100%;
                align-items: center;
                justify-content: center;
            }
        }

        // .secMenu {
        //     display: none;
        // }

        //change here 101
        .secMenu {
            display: block;
            // grid-template-columns: repeat(3, 1fr);
            // gap: 10px;

            .flex {
                display: grid;
                width: 100%;
                margin: 2rem auto;
                justify-content: center;
                grid-template-columns: repeat(3, 1fr);
                gap: 10px;
                // gap: 5px;

                li {
                    background: transparent;
                    padding: 5px;
                    border-radius: var(--borderRadius);
                    color: var(--blackColor);
                    border: 1.5px dashed transparent;
                    cursor: pointer;
                    text-decoration:underline;

                    &:hover {
                        color: var(--primaryColor);
                        border: 1.5px dashed var(--primaryColor);
                    }
                }

                .active {
                    color: var(--primaryColor);
                    border: 1.5px dashed var(--primaryColor);
                }
            }
        }

        //end here 101

        .destinationContainer {
            margin-top: 2rem;

            .singleDestination {
                position: relative;
                overflow: hidden;
                border-radius: 1rem;
                height: 260px;

                .imgDiv {
                    position: relative;
                    height: 100%;
                    width: 100%;

                    img {
                        height: 100%;
                        width: 100%;
                        object-fit: cover;
                        transition: all .3s ease-in-out;
                    }
                }

                &:hover {
                    .imgDiv {
                        img {
                            scale: 1.1;
                        }
                    }
                }

                .descInfo {
                    position: absolute;
                    z-index: 2;
                    bottom: 0;
                    width: 100%;
                    padding: 1rem 2rem 1rem;
                    justify-content: space-between;

                    &::after {
                        content: "";
                        position: absolute;
                        top: 0;
                        right: 0;
                        left: 0;
                        bottom: 0;
                        background: linear-gradient(
                            to bottom,
                            rgba(0, 0, 0, 0),
                            rgba(0, 0, 0, 0.76)
                        );
                        z-index: -1;
                    }

                    .text {
                        gap: .5rem;
                        color: var(--whiteColor);

                        .name {
                            display: block;
                            font-weight: 500;
                            padding-bottom: .3rem;
                            font-weight: var(--h2FontSize);
                        }

                        .flex, .icon {
                            font-size: var(--normalFontSize);
                        }
                    }

                    .rating {
                        padding: 8px 1rem;
                        border-radius: var(--borderRadius);
                        background: rgba(245, 245, 245, 0.37);
                        border: 1.5px solid rgba(255, 255, 255, 0.241);
                        font-size: var(--h3FontSize);
                        font-weight: 600;
                        color: var(--whiteColor);
                    }
                }
            }
        }
    }
}


//media queries
@media (min-width: 520px) {
    .destination {
        .secContainer {
            .searchField {
                grid-template-columns: repeat(2, 1fr);
            }

            .destinationContainer {
                grid-template-columns: repeat(2, 1fr);
            }
        }
    }
}


@media (min-width: 640px) {
    .destination {
        .secContainer {
            .secMenu {
                display: block;
                .flex {
                    display: flex;
                    width: 100%;
                    margin: 2rem auto;
                    justify-content: center;
                    gap: 1rem;

                    li {
                        background: transparent;
                        padding: 10px 17px;
                        border-radius: var(--borderRadius);
                        color: var(--blackColor);
                        border: 1.5px dashed transparent;
                        cursor: pointer;

                        &:hover {
                            color: var(--primaryColor);
                            border: 1.5px dashed var(--primaryColor);
                        }
                    }

                    .active {
                        color: var(--primaryColor);
                        border: 1.5px dashed var(--primaryColor);
                    }
                }
            }

        }
    }
}


@media (min-width: 1014px) {
    .destination {
        .secContainer {

            .secTitle {
                .redText {
                    font-size: var(--normalFontSize);                    
                }

                h3 {
                    font-size: var(--h1FontSize);
                }

                p {
                    font-size: var(--h3FontSize);
                }
            }

            .secMenu {
                .flex {
                    justify-content: left;

                    li {
                        background: transparent;
                        padding: 10px 17px;
                        border-radius: var(--borderRadius);
                        color: var(--blackColor);
                        border: 1.5px dashed transparent;
                        cursor: pointer;

                        &:hover {
                            color: var(--primaryColor);
                            border: 1.5px dashed var(--primaryColor);
                        }
                    }

                    // .active {
                    //     color: var(--primaryColor);
                    //     border: 1.5px dashed var(--primaryColor);
                    // }
                }
            }

            .searchField {
                grid-template-columns: repeat(4, 1fr);
            }

        }
    }
}


@media (min-width: 1148px) {
    .destination {
        .secContainer {

            .secTitle {
                // .redText {
                //     font-size: var(--normalFontSize);                    
                // }

                h3 {
                    font-size: var(--biggestFontSize);
                }

                p {
                    font-size: var(--h2FontSize);
                }
            }

            .destinationContainer {
                grid-template-columns: repeat(3, 1fr);
            }

        }
    }
}


@media (min-width: 1400px) {
    .destination {
        .secContainer {


            .destinationContainer {
                grid-template-columns: repeat(4, 1fr);
            }

        }
    }
}


/* Spinner SCSS */
.spinner {
    border: 4px solid rgba(0, 0, 0, 0.1);
    border-left-color: var(--primaryColor);
    border-radius: 50%;
    width: 40px;
    height: 40px;
    animation: spin 1s linear infinite;
    margin: auto;
    /* Center the spinner */
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  
    @keyframes spin {
      to {
        transform: rotate(360deg);
      }
    }
  }


